@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  top: 0;
  left: 0;
  right: 0;
  font-family: "Libre Baskerville", serif;
}

.App {
  position: fixed;
  overflow: hidden;
}

.body__container {
  display: grid;
  grid-template-columns: 20% 75%;
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
}

.cards__cont {
  grid-column: 2;
  overflow: scroll;
  height: 77vh;
}

.cards__cont::-webkit-scrollbar {
  width: 5px;
  color: yellow;
}
.cards__cont::-webkit-scrollbar-thumb {
  border-radius: 30px;
  width: 5px;
  height: 15px;
  background: #2e2945;
}

.header__container{display:grid;grid-template-columns:repeat(auto-fit, minmax(280px, 1fr));grid-column-gap:15px;-webkit-column-gap:15px;column-gap:15px;height:150px;width:100%;justify-content:center;align-items:center;background-color:#2e2945}.header__container img{box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:10px;margin-left:20px}.header__container input{height:40px;padding-left:10px;border:1px solid #8cade3;box-sizing:border-box;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:10px}.header__container input:focus{outline:1px solid #326ecd}.header__container button{height:40px;width:160px;font-size:18px;background-color:#2e2945;color:white;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:10px;cursor:pointer;background-color:#adc5eb;color:#2e2945;font-weight:700}.header__container button:focus{outline:none}.header__container #hd-submit,.header__container a{justify-self:center}

.sidebar__container{height:bottom;width:250px;background-color:#adc5eb;text-align:center}.sidebar__container h3{margin:0;padding-top:30px}.sidebar__container ul{list-style-type:none;padding:0}.sidebar__container ul li{margin-top:5px}.sidebar__container input{cursor:pointer}

.card_container{height:200px;width:800px;background-color:#e8e8e8;margin-top:20px;border-radius:8px;box-shadow:0px 4px 4px rgba(0,0,0,0.25)}.card_container>div{height:170px;width:730px;padding:15px 30px 15px 40px;display:grid}.card_container article{height:60px;width:inherit;display:grid;align-items:center;grid-template-columns:repeat(auto-fit, minmax(100px, 1fr))}.card_container article h2{grid-column:1 / 2;margin:0px;padding:0}.card_container article img{grid-column:2 / 3}.card_container ul{width:inherit;display:grid;margin-top:8px;padding:0;grid-template-columns:20% 30% 30% 20%;justify-content:start;align-items:center}.card_container ul li{font-size:13px;text-align:start;list-style-type:none}.card_container h5{margin:10px 0 10px}.card_container button{margin:0 335px 0 315px;height:30px;width:80px;font-size:18px;background-color:#2e2945;color:white;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:10px;cursor:pointer}.card_container button:focus{outline:none}ul li span img{width:10px}

.body__cont{display:flex;height:430px;justify-content:center;align-items:center}.job_container{height:380px;width:1000px;background-color:#e8e8e8;border-radius:8px;box-shadow:0px 4px 4px rgba(0,0,0,0.25)}.job_container>div{height:340px;width:860;padding:15px 80px 15px 80px;display:grid;grid-template-rows:20% 10% 55% 15%}.job_container article{width:inherit;display:grid;align-items:center;grid-template-columns:repeat(auto-fit, minmax(100px, 1fr))}.job_container article h2{grid-column:1 / 2;margin:0px;padding:0}.job_container article img{grid-column:2 / 3}.job_container ul{width:inherit;display:grid;margin:0;padding:0;grid-template-columns:repeat(auto-fit, minmax(80px, 1fr));justify-content:start;align-items:center}.job_container ul li{font-size:12px;list-style-type:none}.job_container .scrollable{overflow:hidden scroll}.job_container .scrollable::-webkit-scrollbar{width:3px}.job_container .scrollable::-webkit-scrollbar-thumb{border-radius:30px;width:1px;height:15px;background:#2e2945}.job_container .scrollable .wrapper::-webkit-scrollbar-track{background:transparent}.job_container p{margin:10px 0}.job_container button{height:30px;width:80px;font-size:18px;background-color:#2e2945;color:white;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:10px;cursor:pointer}.job_container button:focus{outline:none}.job_container .buttons{display:grid;grid-template-columns:1fr 1fr;place-items:end center}ul li span img{width:10px}

.view-container{width:100vw;height:100vh;background-color:#2e2945;display:flex;align-content:center;text-align:center;justify-content:space-around;align-items:center}.view-container .form-container{height:450px;width:380px;background-color:#adc5eb;border-radius:10px;box-shadow:0px 4px 4px rgba(0,0,0,0.8)}.form-content{height:90%;width:86%;margin:7%}.form-content form{height:100%;display:grid;align-content:space-between;background-color:#adc5eb}.form-content form>*{margin-bottom:0;background-color:#adc5eb}.form-content form .form-group{display:flex;justify-content:space-between}.form-content form input{height:20px;width:60%;right:0;border-radius:5px;background-color:white}.form-content form #customCheck1{width:auto;height:13px;margin:0}.form-content form label{font-weight:500}.form-content form a,.form-content form p{margin:0 auto}.form-content form label,.form-content form a,.form-content form p,.form-content form .custom-control{background-color:#adc5eb}.form-content form button{height:30px;width:90px;font-size:18px;background-color:#2e2945;color:white;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:10px;cursor:pointer}.form-content form button:focus{outline:none}

textarea{height:100px;width:198px;background-color:white;border-radius:5px}

.page-container{width:100vw;height:100vh;background-color:#2e2945;display:flex;align-content:center;text-align:center;justify-content:space-around}.page-container article{height:300px;width:500px;align-self:center;background-color:#adc5eb;display:grid;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:10px}.page-container article img{justify-self:center;border-radius:10px}.buttons-container{display:flex;justify-content:space-around}.buttons-container button{height:30px;width:90px;font-size:18px;background-color:#2e2945;color:white;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:10px;cursor:pointer}.buttons-container button:focus{outline:none}

